import React, { Component } from 'react';
import{
    Prism
} from '@/components'
import { hot } from 'react-hot-loader';

class View3 extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        function getName() {
            return new Promise((resolve,reject) => {
                setTimeout(() => {
                    console.log('');
                    resolve('');
                }, 1000);
            })
        }

        function getAddress() {
            return new Promise((resolve,reject) => {
                setTimeout(() => {
                    console.log('');
                    resolve('');
                }, 1000);
            })
        }

        async function getUser() {
            let name = await getName();
            let address = await getAddress();
            return '' + name + '\t' + address;
        }
        getUser().then(v => {
            // 2=> 	
            console.log(v);
        });

        return (
            <main>
                <p>Async Generator Function Geneartor Function*asyncyield await</p>
                <p className='indent'>Async Function,Promise</p>
                <p className='indent'>Async Function awaitawait PromiseAsync Await </p>
                <p className='indent'>await async function  </p>
                <strong>Async Function </strong>
                <Prism>{`
                    function getName() {
                        return new Promise((resolve,reject) => {
                            setTimeout(() => {
                                console.log('');
                                resolve('');
                            }, 1000);
                        })
                    }

                    function getAddress() {
                        return new Promise((resolve,reject) => {
                            setTimeout(() => {
                                console.log('');
                                resolve('');
                            }, 1000);
                        })
                    }

                    async function getUser() {
                        let name = await getName();
                        let address = await getAddress();
                        return '' + name + '\t' + address;
                    }
                    getUser().then(v => {
                        // 2=> 	
                        console.log(v);
                    });
                `}</Prism>
            </main>
        );
    }
}
 
export default hot(module)(View3);


